.overlay {
	display: none;
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	background-color: rgba(0,0,0,0.56); }


.modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 380px;
	min-height: 400px;
	background-color: #f2f2f2;
	padding: 38px 40px 40px 40px;

	&__close {
		position: absolute;
		top: 0;
		right: -40px;
		font-size: 41px;
		line-height: 21px;
		color: red;
		cursor: pointer; }

	&__subtitle {
		font-size: 19px;
		color: #0d0d0d;
		text-align: center;
		font-weight: 700; }

	&__descr {
		font-size: 14px;
		color: #0d0d0d;
		text-align: center; }
	&__btn {
		width: 100%;
		height: 54px;
		border: none;
		background-color: #fe5621;
		font-size: 16px;
		color: #ffffff;
		text-align: center;
		border-radius: 5px; }
	&_mini {
		min-height: 140px; } }

#sign-up, #thanks, #details {
	display: none; }
