.feed-form {
	margin-top: 36px;
	display: flex;
	flex-direction: column;
	width: 300px;
	input {
		height: 50px;
		border-radius: 5px;
		margin-bottom: 15px;
		border: none;
		padding: 0 20px;
		font-size: 14px;
		line-height: 66px;
		color: #4b4b4b;
		&:nth-child(3) {
			margin-bottom: 20px; } } }
